@Component({
  selector: 'app-user-home',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, TimeAgoPipe, RouterLink],
  templateUrl: './user-home.component.html',
  styleUrl: './user-home.component.scss'
})
export class UserHomeComponent {
  postForm: FormGroup;
  liked = false;
  uploadedUrls: string[] = [];
  previewUrls: (string | ArrayBuffer | null)[] = [];
  selectedFiles: File[] = [];
  @ViewChild('offcanvasEl') offcanvasEl!: ElementRef;
  toastType: string = 'primary';
  mediaupload: boolean = false;
  validTill: any;
  toggleLike(post: any) {
    if (!post) return;

    // trigger animation
    post.animate = true;

    // optimistic UI update
    const prevLiked = post.isLiked;
    post.isLiked = !prevLiked;
    post.likes += post.isLiked ? 1 : -1;

    // call API
    this.http.post<any>(`${environment.apiUrl}/communities/likes`, { communityId: post.id })
      .subscribe({
        next: (res) => {
          console.log("Like API success:", res);
          if (this.user?.id) {
            this.communityuseridposts(this.user.id);
          }
          this.communityposts();

        },
        error: (err) => {
          console.error("Error in Like post:", err);
          // revert if failed
          post.isLiked = prevLiked;
          post.likes += post.isLiked ? 1 : -1;
        }
      });

    // reset animate flag after animation
    setTimeout(() => { post.animate = false; }, 300);
  }


  user: any | null = null;
  hasSubscription: boolean = false;
  toastMessage: string = '';
  eventsList: any[] = [];
  tagsList: any[] = [];
  uploadProgress: number[] = [];
  socket!: Socket;
  uploadId: string = Date.now().toString();
  posts: any[] = [];
  userposts: any = null;
  s3Url = environment.s3Url;

  @ViewChild('errorToast', { static: false }) errorToast!: ElementRef;
  ngOnInit(): void {
    this.userService.getUser().subscribe(user => {
      this.user = user;
      console.log("user in home", this.user);
      this.hasSubscription = user?.hasSubscription || false;
      if (this.user?.subscriptionType === 'annual' && this.user?.memberShipStartedAt) {
        const start = new Date(this.user.memberShipStartedAt);
        const expiry = new Date(start);
        expiry.setFullYear(start.getFullYear() + 1);
        this.validTill = expiry.toISOString(); // store as ISO string
      } else if (this.user?.subscriptionType === 'lifetime') {
        this.validTill = 'lifetime';
      } else {
        this.validTill = null; // for safety, if no subscription
      }
      
      if (this.user?.id) {
        this.communityuseridposts(this.user.id);
      }
    });
    this.loadEvents();
    this.loadtags();
    this.communityposts();
    // this.socket = io('https://stg.iasca.netoyed.com/api');

    // this.socket.on('connect', () => {
    //   console.log('‚úÖ Connected to socket:', this.socket.id);
    //   this.socket.emit('joinUpload', this.uploadId);
    // });

    // this.socket.on('uploadProgress', (data: any) => {
    //   console.log('üì¶ Upload Progress:', data);

    //   const index = this.selectedFiles.findIndex(f => f.name === data.fileName);
    //   if (index !== -1) {
    //     this.uploadProgress[index] = data.progress;
    //   }
    // });
  }
  ngOnDestroy(): void {
    if (this.socket) this.socket.disconnect();
  }
  ngAfterViewChecked() {
    const left = document.querySelector('.left-side-modules') as HTMLElement;
    const right = document.querySelector('.right-side-modules') as HTMLElement;

    if (left && right) {
      right.style.height = left.offsetHeight + "px";
      right.style.overflowY = "auto";
    }
  }



  constructor(private userService: UserService, private http: HttpClient, private fb: FormBuilder, private toastService: ToastService
  ) {
    this.postForm = this.fb.group({
      caption: ['', [Validators.required, Validators.maxLength(100)]],
      description: [''],
      postTagId: [0, [Validators.required, this.nonZeroValidator]],
      mediaFile: [[]]
    });

  }
  nonZeroValidator(control: any) {
    return control.value && control.value !== 0 ? null : { nonZero: true };
  }
  loadEvents() {
    const token = localStorage.getItem('authToken');
    console.log(token);

    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`,
      'Content-Type': 'application/json'
    });

    this.http.get<any>(`${environment.apiUrl}/events?isActive=true&limit=2`, { headers })
      .subscribe({
        next: (res) => {
          console.log(res);
          this.eventsList = res.data.data;
          console.log(this.eventsList);
        },
        error: (err) => {
          console.error('Error fetching Events:', err);
        }
      });
  }
  loadtags() {
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`,
      'Content-Type': 'application/json'
    });

    this.http.get<any>(`${environment.apiUrl}/post-tags?limit=0`, { headers })
      .subscribe({
        next: (res) => {
          console.log(res);
          this.tagsList = res.data.data;
          console.log(this.tagsList);
        },
        error: (err) => {
          console.error('Error fetching Events:', err);
        }
      });
  }
  copyPostLink(postId: string) {
    const url = `${window.location.origin}/post?id=${postId}`;

    navigator.clipboard.writeText(url).then(() => {
      this.toastService.showToast('Link copied to clipboard ‚úÖ', 'success');
    }).catch(err => {
      console.error('Failed to copy link: ', err);
    });
  }
  communityposts() {
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`
    });

    this.http.get<any>(`${environment.apiUrl}/communities/users?populate=createdBy&limit=50`, { headers })
      .subscribe({
        next: (res) => {
          console.log(res);
          this.posts = res.data.data;
          console.log("postsssss", this.posts);
        },
        error: (err) => {
          console.error('Error fetching posts:', err);
        }
      });
  }
  communityuseridposts(id: number | undefined) {
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`
    });
    console.log("id", id);

    this.http.get<any>(`${environment.apiUrl}/communities/users?populate=postTag&populate=createdBy&createdBy=${id}&limit=1`, { headers })
      .subscribe({
        next: (res) => {
          console.log(res);
          this.userposts = res.data.data[0];
          console.log("userrr posts", this.userposts);
        },
        error: (err) => {
          console.error('Error fetching posts:', err);
        }
      });
  }

  getInitials(user: User | null): string {
    if (!user?.doptOfficer?.englishName) return 'NA';

    // ‚úÖ List of honorifics / prefixes to ignore
    const ignoreList = ['shri', 'mr', 'mrs', 'ms', 'dr', 'prof', 'sir'];

    // ‚úÖ Clean up the name
    const names = user.doptOfficer.englishName
      .trim()
      .split(/\s+/)
      .filter((n: string) => !ignoreList.includes(n.toLowerCase()));

    if (names.length === 0) return 'NA';

    let initials = '';

    if (names.length === 1) {
      // Single word ‚Üí take first 2 letters
      initials = names[0].substring(0, 2).toUpperCase();
    } else {
      // Multi-word ‚Üí take first and last
      initials = names[0][0].toUpperCase() + names[names.length - 1][0].toUpperCase();
    }

    return initials;
  }

  onFileSelected(event: Event): void {
    const input = event.target as HTMLInputElement;
    const files = input.files;
    if (!files || files.length === 0) return;

    Array.from(files).forEach(file => {
      // ‚úÖ Only allow images or videos
      if (!(file.type.startsWith('image/') || file.type.startsWith('video/'))) {
        this.toastService.showToast(
          `Only images and videos are accepted.`,
          'danger'
        );
        return;
      }

      // ‚úÖ Max size 256MB
      if (file.size > 256 * 1024 * 1024) {
        this.toastService.showToast(
          `"${file.name}" exceeds 256MB, Failed to upload.`,
          'danger'
        );
        return;
      }

      // ‚úÖ Prevent duplicates
      if (this.selectedFiles.some(f => f.name === file.name && f.size === file.size)) {
        this.toastService.showToast(
          `"${file.name}" is already selected.`,
          'warning'
        );
        return;
      }

      // Add file
      this.selectedFiles.push(file);

      // Preview
      const reader = new FileReader();
      reader.onload = () => {
        this.previewUrls.push(reader.result as string);
      };
      reader.readAsDataURL(file);

      // üöÄ Upload immediately
      this.uploadFile(file);
    });

    // Reset input so same file can be chosen again
    input.value = '';
  }



  removeFile(index: number): void {
    this.selectedFiles.splice(index, 1);
    this.previewUrls.splice(index, 1);
    this.uploadProgress.splice(index, 1);

    // ‚ùå Previously you used file.name
    // ‚úÖ Now use uploadedUrls (API response URLs)
    this.uploadedUrls.splice(index, 1);

    this.postForm.patchValue({ mediaFile: [...this.uploadedUrls] });
  }

  uploadFile(file: File): void {
    if (!file) return;


    const formData = new FormData();
    formData.append('file', file);

    this.mediaupload = true;
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`
    });


    this.http.post<any>(`${environment.apiUrl}/upload`, formData, { headers }).subscribe({
      next: (result) => {
        console.log('‚úÖ File Uploaded:', result);
        if (result.success && result.data?.url) {
          // Always treat mediaFile as array
          this.mediaupload = false;
          this.uploadedUrls.push(result.data.url);
          this.postForm.patchValue({ mediaFile: [...this.uploadedUrls] });
        }
      },
      error: (err) => {
        console.error('‚ùå Upload failed:', err);
      }
    });
  }


  // ‚úÖ Submit
  onSubmit(): void {
    if (this.postForm.invalid) {
      this.postForm.markAllAsTouched();
      return;
    }
    const desc = this.postForm.get('description')?.value;
    const payload = {
      isActive: true,
      caption: this.postForm.get('caption')?.value,
      description: desc && desc.trim() !== '' ? desc.trim() : null,
      postTagId: Number(this.postForm.get('postTagId')?.value),
      mediaFile: this.postForm.get('mediaFile')?.value // array of file names
    };

    console.log('Form submitted ‚úÖ', payload);

    const token = localStorage.getItem('authToken');
    console.log(token);

    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`,
      'Content-Type': 'application/json'
    });

    this.http.post<any>(`${environment.apiUrl}/communities`, payload, { headers })
      .subscribe({
        next: (res) => {
          console.log(res);
          this.postForm.reset();
          this.selectedFiles = [];
          this.previewUrls = [];
          this.uploadedUrls = [];
          this.postForm.get('postTagId')?.setValue(0);
          this.showToast('‚úÖ Post Submitted Successfully', 'success');
          this.communityposts();
          if (this.user?.id) {
            this.communityuseridposts(this.user.id);
          }
          // close offcanvas
          const offcanvas = bootstrap.Offcanvas.getInstance(this.offcanvasEl.nativeElement);
          offcanvas?.hide();
        },
        error: (err) => {
          console.error('Error fetching communities:', err);
          this.showToast(err.error.message, 'danger');
        }
      });

  }
  showToast(message: string, type: string = 'primary') {
    this.toastMessage = message;
    this.toastType = type;

    const toastEl = document.getElementById('liveToast');
    if (toastEl) {
      const toast = new bootstrap.Toast(toastEl, { delay: 3000 });
      toast.show();
    }
  }
  openEvent(url: string) {
    window.open(url, '_blank'); // opens in new tab
  }
  showErrorToast(message: string): void {
    this.toastMessage = message;

    const toastElement = this.errorToast.nativeElement;
    const toast = new bootstrap.Toast(toastElement);
    toast.show();
  }
}
